html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

p, body {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

form {
  width: 300px;
  margin: 0 auto;
  position: relative;
}

input {
  width: 100%;
  height: 42px;
  border: 3px solid #f9f0da;
  padding-left: 15px;
}

input:focus {
  border-color: #311c24;
}

.search-button {
  height: 42px;
  width: 42px;
  cursor: pointer;
  background: none;
  border: none;
  position: absolute;
  top: 0;
  right: 0;
}

.search-button:before {
  content: "";
  width: 20px;
  height: 20px;
  margin-right: [icon-margin];
  background-image: url("search.5f27059d.svg");
  background-size: cover;
  font-size: 16px;
  display: inline-block;
}

.form-wrap {
  background-color: #683b4d;
  margin: 0;
  padding: 20px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  box-shadow: 0 5px 5px rgba(0, 0, 0, .4);
}

.load-more {
  height: 42px;
  min-width: 200px;
  cursor: pointer;
  color: #fff;
  background-color: #683b4d;
  border: none;
  border-radius: 5px;
  margin: 40px auto;
  font-size: 16px;
  display: block;
  box-shadow: 0 3px 3px rgba(0, 0, 0, .4);
}

